<template>
    <div>
        <headers :title="title"></headers>
        <div class="set-phone">
           <p>首次设置密码，需要验证手机号<br>13333333333</p>
           <div class="conInput clearfix">
                <input class="fl" type="number" placeholder="请输入验证码" />
                <button class="fl" @click='yan'>{{time}}</button>
           </div>
           <div class="btn">验证</div>
           <h6>没收到验证码，试试语音验证</h6>
        </div>
    </div>
</template>

<style lang="less" scoped>
    .set-phone{
        font-family: "PingFang-SC-Medium";
        margin-top: 180px;
        padding-left: 45px;
        p{
            font-size: 30px;
            color: #000;
            padding-bottom: 50px;
            line-height: 46px;
        }
        .conInput{
            font-size: 26px;
            input{
                width: 425px;
                height: 70px;
                padding-left: 25px;
                line-height: 70px;
                border: 1px solid #D4D4D6;
                color: #000;
                border-radius: 10px;
            }
            button{
                width: 180px;
                height: 70px;
                border: 1px solid #653a72;
                border-radius: 10px;
                margin-left: 36px;
                color: #653a72;
            }
        }
        .btn{
            width: 646px;
            height: 78px;
            margin-left: 4px;
            margin-top: 120px;
            background: #653a72;
            text-align: center;
            line-height: 78px;
            border-radius: 10px;
            color: #fff;
            font-size: 30px;
        }
        h6{
            color: #A9A9A9;
            font-size: 22px;
            padding-top: 40px;
            margin-left: 4px;
        }

    }
</style>

<script>
import "lib-flexible"
import headers from '../../components/editHeader.vue'
export default {
  components:{
      headers
  },
  data(){
      return{
          title: "验证手机号",
          time: '获取验证码'
      }
  },
  mounted(){
      document.body.style.backgroundColor = "rgb(255,255,255)";
  },
  methods: {
    yan: function () {
      var that = this;
      var timer = setInterval(function () {
        if (that.time === '获取验证码') {
          that.time = 60;
        }else if (that.time > 0) {
          that.time--
        }else {
          clearInterval(timer)
          that.time = '获取验证码'
        }
      }, 1000)
    }
  }
}
</script>



